import { T } from '@panter/manul-i18n';
import { Col } from 'reactstrap';
import React from 'react';
import SubmitField from 'uniforms-bootstrap4/SubmitField';
import ControlGroup from '../fields/ControlGroup';

const FormActions = ({ submitLabel, children, ...props }) => (
  <ControlGroup>
    {children}
    <SubmitField value={submitLabel} {...props} />
  </ControlGroup>
);


FormActions.displayName = 'FormActions';

FormActions.defaultProps = {
  submitLabel: <T>autoform.defaults.submit</T>,
};

export default FormActions;
